import { Link } from 'react-router-dom';
import { Formik, Form } from 'formik';
import { useFetch } from '../../hooks/fetch.hook';
import { createAuthProvider } from '../../jwt';
import { FormInput } from '../../Components';
import * as yup from 'yup';
import img from '../../assets/img/1.webp';

const validationSchema = yup.object({
	email: yup
		.string()
		.email('  email')
		.required('Email  '),
	password: yup
		.string()
		.min(4, '  6')
		.required('  '),
});

export const Login = () => {
	const { loading, request } = useFetch();
	const { login } = createAuthProvider();

	const formHandler = (values) => {
		request('/api/auth/login', 'POST', values, {}, (data) => {
			login(data);
			document.location.reload();
		});
	};

	return (
		<main className='sign'>
			<img className='sign__bg' src={img} alt='   ' />

			<div className='sign__info'>
				<h1 className='sign__logo'>BestTutor</h1>

				<Formik
					initialValues={{
						email: '',
						password: '',
					}}
					validationSchema={validationSchema}
					onSubmit={(values) => {
						formHandler(values);
					}}>
					<Form className='form form--sign'>
						<h2 className='form__title'></h2>
						<fieldset className='form__fieldset'>
							<FormInput label='Email ' name='email' type='email' />
						</fieldset>
						<fieldset className='form__fieldset'>
							<FormInput
								label=''
								html="<a className='form__link' href='#'> ?</a>"
								name='password'
								type='password'
							/>
						</fieldset>
						<button
							className='btn form__btn w-100'
							type='submit'
							disabled={loading}>
							
						</button>
					</Form>
				</Formik>

				<footer className='sign__footer'>
					 {' '}
					<Link className='sign__link' to='/signup'>
						
					</Link>
				</footer>
			</div>
		</main>
	);
};
