import { Link } from 'react-router-dom';
import { Formik, Form } from 'formik';
import { useFetch } from '../../hooks/fetch.hook';
import { FormInput, FormSelect } from '../../Components';
import * as yup from 'yup';
import img from '../../assets/img/2.webp';

const validationSchema = yup.object({
	isTeacher: yup.string(),
	email: yup
		.string()
		.email('  email')
		.required('Email  '),
	username: yup
		.string()
		.max(30, '  30')
		.required('   '),
	dateOfBirth: yup.string().required(' '),
	password: yup
		.string()
		.min(6, '  6')
		.required('Password  '),
});

export const Signup = () => {
	const { loading, request } = useFetch();

	const formHandler = (values) => {
		request('/api/auth/register', 'POST', values, {}, (data) => {
			request('/api/auth/sendValidation', 'POST', {
				email: values.email,
			});
			alert(data.message);
		});
	};

	return (
		<main className='sign'>
			<img className='sign__bg' src={img} alt='   ' />

			<div className='sign__info'>
				<h1 className='sign__logo'>BestTutor</h1>

				<Formik
					initialValues={{
						isTeacher: 'false',
						email: '',
						username: '',
						dateOfBirth: '',
						password: '',
					}}
					validationSchema={validationSchema}
					onSubmit={(values) => {
						formHandler(values);
					}}>
					<Form className='form form--sign'>
						<h2 className='form__title'></h2>
						<fieldset className='form__fieldset'>
							<FormSelect label=' ' name='isTeacher'>
								<option value='false'></option>
								<option value='true'></option>
							</FormSelect>
						</fieldset>
						<fieldset className='form__fieldset'>
							<FormInput label='Email ' name='email' type='email' />
						</fieldset>
						<fieldset className='form__fieldset form__fieldset--flex'>
							<fieldset className='form__fieldset'>
								<FormInput label='  ' name='username' type='text' />
							</fieldset>
						</fieldset>
						<fieldset className='form__fieldset'>
							<FormInput label=' ' name='dateOfBirth' type='date' />
						</fieldset>
						<fieldset className='form__fieldset'>
							<FormInput label='' name='password' type='password' />
						</fieldset>
						<button
							className='btn form__btn w-100'
							type='submit'
							disabled={loading}>
							
						</button>
					</Form>
				</Formik>

				<footer className='sign__footer'>
					 ?{' '}
					<Link className='sign__link' to='/login'>
						
					</Link>
				</footer>
			</div>
		</main>
	);
};
